const SquidWrapper = styled.div`
  width: 440px;
  height: 700px;
  @media (max-width: 768px) {
    width: 95%;
  }
`

export default function SquidSwap() {
  const { isDark } = useTheme()

  return (
    <>
      <CardNav activeIndex={2} />
      {/* <StyledIframe
        title="squid_widget"
        src={isDark ? darkUrl : whiteUrl}
      /> */}
      <SquidWrapper>
        <SquidWidget
          config={{
            integratorId: 'ArcherSwap-swap-widget',
            companyName: 'ArcherSwap',
            apiUrl: 'https://api.0xsquid.com',
            style: isDark ? darkModeStyle : lightModeStyle,
            // initialFromChainId: 137,
            // initialToChainId: 43114,
            loadPreviousStateFromLocalStorage: true,
            // preferDex: [ARCHERSWAP, ARCHERSWAP_V3],
          }}
        />
      </SquidWrapper>
    </>
  )
}
